
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
export default {
components: {
XHeader,
XInput, Group,
XButton,
PopupPicker,
Toast
},
data() {
return {
userName:'',
phone:'',
sex:'',
address:'',
inviteName:'',
sexVal:[],
sexDatas:[
{
name:'男',
value:1,
parent: 0
},
{
name:'女',
value:0,
parent: 0
}
]
}
},
created(){
document.title="申请会员"
},
mounted(){

},
methods:{
submitBtn(){

},
register(){
if(this.userName==''){
this.$vux.toast.show({
text: '请填写姓名',
type: 'text',
})
return false
}
if(!this.phone){
this.$vux.toast.show({
text: '请填写手机号',
type: 'text',
})
return false
}
if (/^1[3|4|5|6|7|8|9][0-9]{1}[0-9]{8}$/.test(this.phone) == false) {
this.$vux.toast.show({
text: '手机号格式不正确',
type: 'text',
})
return false
}
if(this.sexVal.length==0){
this.$vux.toast.show({
text: '请选择性别',
type: 'text',
})
return false
}
if(!this.address){
this.$vux.toast.show({
text: '请填写地址',
type: 'text',
})
return false
}







var datas = {
"openId":getStore('openId'),
"userName":this.userName,
"phone":this.phone,
"sex":this.sexVal[0],
"address":this.address,
"inviteName":this.inviteName
}
this.$http.post(this.HttpUrl.UrlConfig.register,datas)
.then(res => {
var res = res.data
if(res.code=='1'){
this.$router.push(
{
path: '/apply-sucess'
})
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}

}).catch(error => {
console.log(error)
})
}
}
}




