
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import {mapMutations} from 'vuex'
export default {
components: {
Toast
},
data() {
return {
items:[],
i: -1,
orderNo:'',
page:''
}
},
created(){
document.title= "我的地址"

},
mounted(){
if(getUrlKey('orderNo')){
this.orderNo = getUrlKey('orderNo')
setStore('orderNo',  this.orderNo)
}
if(getUrlKey('page')){
this.page = getUrlKey('page')
}
this.addressList()

},
methods:{
addressList(){
let datas = {
"openId":getStore('openId')
}
this.$http.post(this.HttpUrl.UrlConfig.address,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 会员
this.items = res.data
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
},
...mapMutations([
'SEL_ADDRESS'
]),
imgShowClick(item,index){
this.changeOrderAddress(item)
this.i = index
if(this.page=='comfirmOrder'){
setStore('comfirmOrderAddress',item)
}

let datas = {
"openId":getStore('openId'),
"orderNo":this.orderNo,
"orderNoAddress":item.address
}
this.$http.post(this.HttpUrl.UrlConfig.orderNoAddress,datas)
.then(res => {
var res = res.data
if(res.code=='1'){
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text'
})
}
}).catch(error => {
console.log(error)
})
},
changeOrderAddress(item){
let datas = {
'openId':getStore('openId'),
'orderNo':getUrlKey('orderNo'),
'person':item.person,
'phone':item.phone,
'address':item.address,
}
this.$http.post(this.HttpUrl.UrlConfig.changeOrderAddress,datas)
.then(res => {
var res = res.data
if(res.code=='1'){                              console.log('选择成功！')
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text'
})
}
}).catch(error => {
console.log(error)
})
},
updateAddress(item){
setStore('updateAddress',item)
this.$router.push({
path:'/news-address'
})
}
}

}




