
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XInput,
Group,
Cell,
Toast,
XButton
},
data(){
return{
toastMsg:'',
toastShow:false,
takeCode:'',
goodName:'',
goodImg:'',
goodBatch:'',
reserveMember:'',
userName:'',
date:'',
phone:'',
address:'',
goodId:''
}
},
created(){
document.title="确认提货"
},
mounted(){
this.takeGoodInfo()
},
methods:{
takeGoodInfo(){
let datas = {
'openId': getStore('openId'),
'takeCode': getUrlKey('takeCode')
}
this.$http.post(this.HttpUrl.UrlConfig.takeGood, datas)
.then(res => {
var res = res.data
if (res.code == "1") {
this.takeCode = res.data.takeCode
this.goodName = res.data.goodName
this.goodImg = res.data.goodImg
this.reserveMember = res.data.reserveMember
this.goodBatch = res.data.goodBatch
this.date = '预计发货时间：'+res.data.sendTime
this.goodId = res.data.goodId
} else {
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
},
confirmTakeGood(){







var datas = {
"openId":getStore('openId'),
"takeCode":getUrlKey('takeCode'),
"address":this.address,
"person":this.userName,
"phone":this.phone,
"goodId":this.goodId,
"reserveMember":this.reserveMember
}
this.$http.post(this.HttpUrl.UrlConfig.confirmTakeGood,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 成功
let result = res.result
let that = this;

this.$vux.toast.show({
text: '提货成功',
type: 'text',
width: '80%',
time:'2000',
onHide(){
that.$router.push(
{
path: '/goods-sucess',
query: { result:  result}
})
}
})


}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}

}).catch(error => {
console.log(error)
})
}
}
}




