
import {
setStore, getStore
}
from '../../config/mUtils'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Toast
},
data() {
return {
userName:'',
userPhone:'',
toastTxt: '',
toastShow: false,
nums: [{
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}],
}
},
created(){
document.title="加入会员"
},
mounted(){


},
methods:{
inviteCode(){
let takeCode = '',
flag = true
this.nums.forEach((val, index) => {
if (!val.num) {
this.$vux.toast.text('请正确输入邀请码')
flag = false
return false
}
takeCode += val.num
})


if (flag) {

if(this.userName==''){
this.$vux.toast.show({
text: '请填写姓名',
type: 'text',
})
return false
}
if(!this.userPhone){
this.$vux.toast.show({
text: '请填写手机号',
type: 'text',
})
return false
}
if (/^1[3|4|5|6|7|8|9][0-9]{1}[0-9]{8}$/.test(this.userPhone) == false) {
this.$vux.toast.show({
text: '手机号格式不正确',
type: 'text',
width: '80%'
})
return false
}



let datas = {
"openId":getStore('openId'),
"inviteCode":takeCode,
"name":this.userName,
"phone":this.userPhone
}
console.log(datas)
this.$http.post(this.HttpUrl.UrlConfig.inviteCode,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 会员
this.$router.push(
{
path: '/join-members-sucess',
query:{
result:res.result
}
})
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
}


}
}
}




