
import Vue from 'vue'
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'


import {mapMutations} from 'vuex'














export default {
directives: {
TransferDom
},
components: {
Swiper,
XDialog,
XButton,
Flexbox,
FlexboxItem,
Group,
XNumber
},
data() {
return {
goodTitle:'',
goodSubTitle:'',
goodPrice:'',
goodAddress:'',
goodDes:'',
goodIcon:'',
goodsDatas:[],
show: false,
dialogShow: false,
myAddress: false,
demo01_list: [],
demo01_index: 0,
total: 0,
numValue:1,
price:0,
thumb:'',
goodName:'',
goodId:'',
goodNum:null,
goodSpecId:null
}
},
created(){
if(getUrlKey('openId')){
setStore('openId',getUrlKey('openId'))
}
},
mounted() {
this.getGoods()
},
methods: {
...mapMutations([
'ADD_CART'
]),
getGoods(){
var datas = {
"openId":getStore('openId')
}

this.$http.post(this.HttpUrl.UrlConfig.getGoods,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 成功


this.goodName = res.data[0].goodName
this.goodTitle = res.data[0].goodTitle
this.goodSubTitle = res.data[0].goodSubTitle
this.goodPrice = res.data[0].goodPrice
this.goodAddress = res.data[0].goodAddress
this.goodDes = res.data[0].goodDes
this.goodIcon = res.data[0].goodIcon
this.goodsDatas = res.data[0].goodSpec
this.thumb = res.data[0].thumb
this.goodId = res.data[0].goodId


this.$nextTick(()=>{
Vue.set(this.goodsDatas[0],'active',true)
this.price = this.goodsDatas[0].price
this.goodSpecId = this.goodsDatas[0].id
this.goodSpeName = this.goodsDatas[0].name
this.total = parseInt(this.numValue) * this.price
})








let advImgStr = res.data[0].advImg.split(',')



let advImgArr = []
advImgStr.forEach((val)=>{

advImgArr.push({
url: 'javascript:',
img: val,
title: ''
})
})
this.demo01_list = advImgArr
}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}

}).catch(error => {
console.log(error)
})
},
demo01_onIndexChange(index) {
this.demo01_index = index
},

addAddress() {
this.dialogShow = true

},
change(val) {
this.goodNum = parseInt(val)
console.log('change', val)
this.total = parseInt(val) * this.price
},
handlerBuyBtn() {
this.show = true
},
handlerCloseBtn(){
console.log(11)
this.show = false
},
selGood(item){
let that = this
this.goodSpecId = item.id
this.goodSpeName = item.name
this.price = item.price
console.log(item.id)
this.$nextTick(()=>{
this.goodsDatas.forEach((item)=>{
Vue.set(item,'active',false)
})
Vue.set(item,'active',true)
this.price = item.price
this.total = parseInt(this.numValue) * this.price
})
this.numValue = 1
},
subCart(){

let datas = {
'openId':getStore('openId'),
'goodId':this.goodId,
'goodNum':this.goodNum,
'goodSpecId':this.goodSpecId,
'goodSpeName':this.goodSpeName,
'price':this.price,
'thumb':this.thumb,
'goodName':this.goodName,
'goodPrice':this.total,







}
setStore('buyInfo',datas)

this.$router.push({
path:'/confirm-order'
})
console.log(this.price)
}
}
}




