

import {
setStore, getStore
}
from '../../config/mUtils'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XButton
},
data() {
return {
nums: [{
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}, {
num: ''
}],
reserveMember: ''
}
},
created() {
document.title = "提货密码"
},
mounted() {

},
methods: {
takeGood() {

let takeCode = '',
flag = true
this.nums.forEach((val, index) => {
if (!val.num) {
this.$vux.toast.text('请正确输入提货密码')
flag = false
return false
}
takeCode += val.num
})
if (flag) {

let datas = {
'openId': getStore('openId'),
'takeCode':takeCode
}
this.$http.post(this.HttpUrl.UrlConfig.takeGood, datas)
.then(res => {
var res = res.data
if (res.code == "1") {
this.$router.push({
path: '/comfirm-goods',
query:{
takeCode:takeCode
}
})
} else {
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
}


},
handelClick() {

},
clickKeyup(n,index){



}
}
}




