
import utils from '../../assets/js/urlConfig.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
export default {
components: {
Group, Cell,Toast
},
data() {
return {
noOrderTxt:'暂无订单',
orderList:false,
items: [],
toastTxt: '',
toastShow: false,
page:'1',
addressInfo:'修改收货信息',
orderNo:''
}
},
created() {
document.title = "我的订单"
},
mounted() {









this.myorder()
},
methods:{
myorder(){
var datas = {
"openId":getStore('openId'),
"page":this.page
}
this.$http.post(this.HttpUrl.UrlConfig.myorder,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 成功
this.items = res.data
this.orderList = true
}else{ // 异常
this.toastShow = true
this.toastTxt = res.msg
}

}).catch(error => {
console.log(error)
})
}
}
}




