
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
export default {
components: {
Group, Cell,Toast
},
data() {
return {
userName:'',
phone:'',
userSex:'',
addres:''
}
},
created(){
document.title="会员"
if(getUrlKey('openId')){
setStore('openId',getUrlKey('openId'))
}
},
mounted(){
this.getUserMessage()
},
methods:{
getUserMessage(){
var datas = {
"openId":getStore('openId')
}
this.$http.post(this.HttpUrl.UrlConfig.getUserMessage,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 成功
this.userName = res.data.userName
this.phone = res.data.phone
this.userSex = res.data.userSex
this.address = res.data.address
}else{ // 异常
this.toastShow = true
this.toastTxt = res.msg
}
}).catch(error => {
console.log(error)
})
}
}
}




