import qs from 'qs'
import {
    setStore, getStore ,getUrlKey
}
from '../../config/mUtils'

export default {

    data() {
        return {
          orderPrice:'',
          orderNo:'',
          payTime:'',
          person:'',
          phone:'',
          address:'',
          receiver:'',
          batch:''
        }
    },
    created(){
        document.title="支付成功"
    },
    mounted() {
      this.orderNo = getStore('orderNo')
      this.payGoodsSuccess()
    },
    methods: {
      payGoodsSuccess(){
        let datas = {
            'openId':getStore('openId') ,
            'orderNo':this.orderNo
        }
          this.$http.post(this.HttpUrl.UrlConfig.payGoodsSuccess, qs.stringify(datas))
              .then(res => {
                  var res = res.data
                  if (res.code == "1") {
                    this.orderPrice = res.orderPrice
                    this.payTime = res.payTime
                    this.person = res.person
                    this.phone = res.phone
                    this.address = res.address
                    this.receiver = res.receiver
                    this.batch = res.batch
                  } else {
                      this.$vux.toast.show({
                          text: res.msg,
                          type: 'text',
                          width: '80%'
                      })
                  }
              }).catch(error => {
                  console.log(error)
              })
      }
    }
}




