
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XHeader,
XInput, Group,
XButton
},
data() {
return {
username:'',
phone:'',
address:'',
id:'0',
msg:'保存成功'
}
},
created(){

},
mounted(){
if(getStore('updateAddress')){
let updateAddress = JSON.parse(getStore('updateAddress'))
this.username = updateAddress.person
this.phone = updateAddress.phone
this.address = updateAddress.address
this.id = updateAddress.id
this.msg = '修改成功'
document.title="修改地址"
}else{
document.title="新增地址"
}
},
methods:{

selAddress(){
if(this.username==''){
this.$vux.toast.show({
text: '请填写姓名',
type: 'text',
})
return false
}
if(!this.phone){
this.$vux.toast.show({
text: '请填写手机号',
type: 'text',
})
return false
}
if (/^1[3|4|5|6|7|8|9][0-9]{1}[0-9]{8}$/.test(this.phone) == false) {
this.$vux.toast.show({
text: '手机号格式不正确',
type: 'text',
width: '80%'
})
return false
}

if(!this.address){
this.$vux.toast.show({
text: '请填写收货地址',
type: 'text',
})
return false
}


let datas = {
"openId":getStore('openId'),
"id":this.id,
"person":this.username,
"phone":this.phone,
"address":this.address

}
this.$http.post(this.HttpUrl.UrlConfig.selAddress,datas)
.then(res => {
let that = this;
var res = res.data
if(res.code=='1'){// 会员
this.$vux.toast.show({
position:'top',
text: this.msg,
type: 'text',
width: '80%',
time:'2000',
onHide(){
that.$router.push({
path:'/address-list',
query:{
orderNo:getUrlKey('orderNo')
}
})
}
})

}else{ // 异常
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
}

}
}




